/* FIXME: переписать на препроцессоры Aside */

.wrapper {
  display: flex;
  flex-direction: column;
  height: calc(100% - 55px);


  .container {
    display: flex;
    position: relative;
    height: 100%;

    .aside {
      overflow-y: auto;
      height: 100%;
      width: 300px;

      @media (max-width: 768px) {
        width: 100%;
      }
    }

    .mainContent {
      width: 100%;
      overflow-y: auto;

      
    }
  }
}

/* width */

::-webkit-scrollbar {
  width: 5px;
}

/* Track */

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */

::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
  background: #555;
}
